    //     .then((res) => {
    //       console.log('res', res);
    //       if (res.status === 200) {
    //         setLoading(false);
    //         if (res && res.data && res.data.choices && res.data.choices[0] && res.data.choices[0].message && res.data.choices[0].message.content) {
    //           localStorage.setItem('data', res.data.choices[0].message.content);
    //           navigate('/plan');
    //         }
    //       }
    //     })
    //     .catch((err) => {
    //       setLoading(false);
    //       messageApi.open({
    //         type: 'error',
    //         content: err.message,
