import './index.css';
import { useNavigate } from 'react-router-dom'
import ReactMarkdown from 'react-markdown'
import remarkGfm from 'remark-gfm'
import {
  Button,
} from 'antd';
function Plan () {
	const navigate = useNavigate();
	const data = localStorage.getItem('data');

	const backHome = ()=> {
		navigate('/home');
	}
	// console.log('data', data);
	return (
		data && <div className="plan">
			<h3>, ~</h3> 
			<a href="https://www.wjx.cn/vm/mWPzZcY.aspx#" target="_blank" className='feedback_link' rel="noreferrer"></a>
			<ReactMarkdown className='plan-content' children={data} remarkPlugins={[remarkGfm]} />
			<Button type="primary" className='back_home' onClick={backHome}></Button>
		</div>
  );
};

export default Plan;